.App {
  font-family: Arial, sans-serif;
  text-align: center;
  margin: 0;
  padding: 20px;
  background-color: #f77f00;
  min-height: 100vh; 
  color: white; 
}

.pokemon-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  padding: 16px;
}

.pokemon-card {
  background-color: lightgray;
  border-radius: 12px;
  padding: 16px;
  text-align: center;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.pokemon-card:hover {
  transform: scale(1.05);
}

.pokemon-details-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.pokemon-details {
  background: white;
  padding: 20px;
  border-radius: 12px;
  text-align: center;
  max-width: 400px;
  width: 100%;
  position: relative;
  color: black; 
}

.pokemon-details img {
  width: 150px;
  height: 150px;
  margin: 10px 0;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.pokemon-logo {
  width: 200px; 
  margin: 20px auto 10px; 
  display: block; 
}

.search-bar {
  width: 100%;
  max-width: 300px; 
  padding: 10px;
  margin: 0 auto 20px; 
  border: 2px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
  display: block; 
}


